@import 'var.scss';
@import 'general.scss';

.wrapper {
	display: none;
	border: 15px solid #333;
	@media (max-width: 480px) {
		border-width: 10px;
	}
	&--inner {
		&:before {
			content: '';
			width: 100%;
			height: 15px;
			display: block;
			position: fixed;
			top: 0;
			left: 0;
			background-color: #333;
			z-index: 10;
			@media (max-width: 480px) {
				height: 10px;
			}
		}
		&:after {
			content: '';
			width: 100%;
			height: 15px;
			display: block;
			position: fixed;
			bottom: 0;
			left: 0;
			background-color: #333;
			@media (max-width: 480px) {
				height: 10px;
			}
		}
	}
}
.main {
	// background-color: #212121;
	background-color: #fff;
	color: #333;
	width: 100%;
  height: calc(100vh - 30px);
	transform-origin: center;
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 30px 60px;
	// min-height: 700px;
	// background-image: url(../images/1.jpg);
	// background-position: top 42% right 15%;
	// background-repeat: no-repeat;
	@media (max-width: 480px) {
		padding: 30px 40px;
		height: calc(100vh - 20px);
	}
	&_text-wrap {
		// width: 100%;
		// height: 100%;
		display: flex;
		justify-content: center;
		flex-direction: column;
		margin-right: 50px;
		@media (max-width: 768px) {
			margin-right: 0;
		}
		// padding: 30px 140px;
	}
}
.main-photo {
	width: 400px;
	height: 516px;
	background-color: #003adc;
	flex-shrink: 0;
	@media (max-width: 1350px) {
		width: 300px;
		height: 360px;
	}
	@media (max-width: 1024px) {
		display: none;
	}
	// position: absolute;
	// top: 50%;
	// right: 120px;
	// transform: translateY(-50%);
	z-index: 1;
	border-top: 6px solid #003adc;
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: right;
	}
}
.section {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
	&_text-wrap {
		color: #fff;
		position: absolute;
		top: 30%;
		left: 50%;
		padding-right: 20px;
		@media (max-width: 1100px) {
			padding-left: 40px;
		}
		@media (max-width: 768px) {
			padding-left: 20px;
		}
		@media (max-width: 480px) {
			left: 0%;
			width: 55%;
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	&_img-wrap {
	  position: absolute;
	  clip: rect(auto,auto,auto,auto);
	  width: 50%;
	  height: 100%;
	  top: 0;
	  left: 0;
		padding-left: 20px;
		@media (max-width: 480px) {
			left: 55%;
		}
		@media (max-width: 340px) {
			left: 45%;
			width: 55%;
		}
	}
	&:nth-child(odd) {
		background: #ebebeb;
	}
	&:nth-child(even) {
		// background: #1d1e1f;
		background: #414141;
		.card {
			color: #f8f8f8;
		}
		.card_subtitle {
			// color: #7c8694;
			color: #fff;
		}
		.technologies_item {
			// color: #535e6e;
			// border-color: #535e6e;
		}
	}
}


.mac {
  position: fixed;
  top: 25%;
  left: 45%;
	margin-left: -560px;
  z-index: 99;
	@media (max-width: 1300px) {
		margin-left: -460px;
	}
	@media (max-width: 1100px) {
		margin-left: -360px;
		left: 50%;
	}
	@media (max-width: 768px) {
		margin-left: -240px;
		top: 34%;
	}
	@media (max-width: 480px) {
		margin-left: -160px;
		left: 100%;
		top: 35%;
	}
}
.mockup {
	width: 560px;
  position: relative;
  z-index: 99;
	@media (max-width: 1300px) {
		width: 460px;
	}
	@media (max-width: 1100px) {
		width: 360px;
	}
	@media (max-width: 768px) {
		width: 240px;
	}
	@media (max-width: 480px) {
		width: 140px;
	}
	img {
		max-width: 100%;
	}
}
.work {
	width: 512px;
	height: 288px;
  position: absolute;
	top: 24px;
	left: 24px;
  z-index: 9;
	@media (max-width: 1300px) {
		width: 422px;
		height: 239px;
		top: 18px;
		left: 18px;
	}
	@media (max-width: 1100px) {
		width: 330px;
		height: auto;
		top: 15px;
		left: 15px;
	}
	@media (max-width: 768px) {
		width: 222px;
		top: 10px;
		left: 10px;
	}
	@media (max-width: 480px) {
		width: 130px;
		top: 5px;
		left: 5px;
	}
	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}


.card {
	padding: 20px 0 0 0;
	color: #1d1e1f;
	z-index: 10;
	position: relative;
	@media (max-width: 1024px) {
		padding-top: 0;
	}
	&_title {
		// text-transform: uppercase;
		font-size: 60px;
		font-weight: 400;
		margin-bottom: 10px;
		@media (max-width: 1100px) {
			font-size: 40px;
		}
		@media (max-width: 1024px) {
			font-size: 32px;
		}
		@media (max-width: 768px) {
			font-size: 24px;
		}
		@media (max-width: 480px) {
			font-size: 20px;
			margin-bottom: 5px;
		}
	}
	&_subtitle {
		display: block;
		margin-bottom: 30px;
		// text-align: right;
		color: #535e6e;
		font-size: 1rem;
		@media (max-width: 1100px) {
			text-align: left;
		}
		@media (max-width: 768px) {
			font-size: 11px;
			margin-bottom: 15px;
		}
	}
}

.technologies {
	margin-bottom: 20px;
	display: flex;
	// justify-content: flex-end;
	flex-wrap: wrap;
	@media (max-width: 768px) {
		justify-content: flex-start;
	}
	&_item {
		color: #9a9a9a;
		border: 1px solid #9a9a9a;
		border-radius: 5px;
		padding: 3px 10px;
		font-size: 10px;
		margin-right: 6px;
		margin-bottom: 6px;
		text-transform: uppercase;
		cursor: default;
		font-family: 'Roboto Mono', monospace;
		@media (max-width: 768px) {
			padding: 2px 6px;
			margin-right: 3px;
			margin-bottom: 3px;
			font-size: 8px;
		}
		&:last-child {
			margin-right: 0;
		}
		&:hover {
			color: #535e6e;
			border-color: #535e6e;
		}
	}
}

.links {
	text-align: right;
	@media (max-width: 480px) {
		text-align: left;
	}
	&_item {
		margin-right: 20px;
		text-decoration: none;
		font-size: 16px;
		// color: #da8f83;
		padding: 5px 20px!important;
		display: block;
		// text-transform: uppercase;
		&:last-child {
			margin-right: 0;
		}
		// &:hover {
		// 	color: darken(#da8f83, 10%);
		// }
	}
}



.wrapper, .main {
  transition-property: transform;
  transition-duration: .5s;
}

.wrapper.move-out, .main.move-out {
  transform: scale(0.6);
}

.main-nav {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	// background: #eeecf5;

	background: #212121;
	-webkit-transform: translateZ(0);
	-webkit-backface-visibility: hidden;
	z-index: 100;
	transform: translateY(-100%);
	transition-property: transform;
	transition-duration: .5s;
	// font-family: 'Roboto Mono', monospace;
	&.is-visible {
	  transform: translateY(0);
	}
	ul {
		height: 100%;
		list-style: none;
		padding: 0;
		margin: 0;
	}

	li {
		height: 20%;

		a {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: flex-start;
			padding: 0 10%;
			height: 100%;
			border-bottom: 1px solid darken(#212121, 5%);
			color: #cfcfcf;
			// font-weight: bold;
			font-size: 20px;
			text-decoration: none;
			// text-transform: uppercase;
			// transition: all 0.2s ease;
			@media (max-width: 1024px) {
				padding: 0 5%;
			}
			@media (max-width: 480px) {
				font-size: 16px;
			}
			&:hover {
				background-color: lighten(#212121, 2%);
			}
		}

		&:last-child a {
			border-bottom: none;
		}
	}

	.cd-close-menu {
		position: absolute;
		top: 0;
		right: 0;
		display: inline-block;
		width: 40px;
		height: 40px;
		background-color: #303030;

		/* image replacement */
		overflow: hidden;
		text-indent: 100%;
		white-space: nowrap;
		transition: all 0.2s ease;
		&:hover {
			background-color: #d26c64;
		}
		&::before, &::after {
			content: '';
			display: inline-block;
			position: absolute;
			top: 18px;
			left: 10px;
			width: 20px;
			height: 3px;
			background-color: #FFF;
			transition: all 0.3s ease;
		}
		&::before {
			transform: rotate(45deg);
		}

		&::after {
			transform: rotate(-45deg);
		}
	}

	&.is-visible {
		box-shadow: 0 0 20px rgba(#000, .4);
	}
}



.btn-back {
	position: fixed;
	top: 30px;
	left: 30px;
	color: #000;
	text-decoration: none;
	font-size: 28px;
	z-index: 200;
	padding: 0 20px;
	color: #003adc;
	background-color: #f1f1f1;
	font-weight: 500;
	@media (max-width: 1024px) {
		font-size: 18px;
		top: 20px;
		left: 20px;
	}
	@media (max-width: 480px) {
		font-size: 16px;
		top: 18px;
		left: auto;
		right: 18px;
	}
	&:hover {
		background: darken(#f1f1f1, 5%);
	}
}

.about {
	margin-bottom: 40px;
	text-align: left;
	font-size: 50px;
	font-weight: 400;
	// font-family: 'Josefin Sans', sans-serif;
	font-family: 'Poppins', sans-serif;
	max-width: 900px;
	z-index: 2;
	position: relative;
	@media (max-width: 1350px) {
		font-size: 30px;
	}
	@media (max-width: 480px) {
		font-size: 22px;
	}
	span {
		display: block;
		font-size: 120px;
		left: -40px;
		position: relative;
		@media (max-width: 1350px) {
			font-size: 60px;
		}
		@media (max-width: 1024px) {
			font-size: 50px;
		}
		@media (max-width: 480px) {
			font-size: 30px;
			left: -15px;
		}
		// margin-bottom: 10px;
	}
	a {
		// color: #da8f83;
		color: #003adc;
		text-decoration: none;
		font-weight: 400;
	}
	p {
		margin: 5px 0;
	}
}

.open-contact, .links_item {
	display: inline-block;
	padding: 0 20px;
	color: #003adc;
	border: none;
	background-color: #f1f1f1;
	font-weight: 500;
	cursor: pointer;
	&:hover {
		background: darken(#f1f1f1, 5%);
	}
}

.links_item {
	@media (max-width: 1024px) {
		padding: 0 10px;
		font-size: 10px;
	}
}
